<template>
    <div id="app">
   </div>
</template>

<script>
    import { GenerateTarget } from "@/scripts/TargetGenerator";

    export default {
        name: 'App',
        data() {
            return {
                targets: undefined,
            };
        },
        mounted() {
            this.Compile()
        },
        methods: {
            async Compile() {
                this.targets = await GenerateTarget(["./target.png"]);
                this.$emit("targets", this.targets);
            }
        }
    }
</script>

<style>
    @import '@/css/styles.css';
    @import 'purecss/build/pure.css';

    #container {
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 0;
    }

    #unity-canvas {
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1;
    }
</style>
