<template>
    <div id="app">
        <div v-if="!IsFaceTracking()">
            <Compiler @targets="CompiledTargets" />
        </div>
        <canvas id="unity-canvas"></canvas>
        <div id="container" ref="container" :class="{fliped: IsFaceTracking()}"></div>
    </div>
</template>

<script>
    //import "mind-ar/dist/mindar-image-three.prod.js";
    import { Init } from '@/scripts/index.js';
    import Compiler from './Compiler.vue';

    export default {
        name: 'App',
        components: {
            Compiler
        },
        data() {
            return {
                targets: undefined,
                FACE_TRACKING: "{{{IS_FACE_TRACKING}}}",
            };
        },
        mounted() {
            this.getMediaDevice();
        },
        methods: {
            IsFaceTracking() {
                if (this.FACE_TRACKING.toLowerCase() == "true") {
                    return true;
                }
                return false;
            },
           async CompiledTargets(result) {
                this.targets = result;
            },
            OnError(errorMessage) {
                return console.error(errorMessage);
            },
            async getMediaDevice() {
                if (navigator?.mediaDevices?.getUserMedia) {
                    try {
                        await navigator.mediaDevices.getUserMedia({
                            video: {
                                facingMode: { exact: "environment" },
                            },
                        }).then((stream) => {
                            console.error(stream);
                        });
                    } catch (err) {
                        this.OnError(err);
                    }
                } else {
                    return this.OnError("Can`t get access to any user`s media device. Probably connection is unsecured ('https' protocol required)")
                }

                //Run Scene
                this.LoadScene();
            },
            async LoadScene() {
                let canvasUnity = document.querySelector("#unity-canvas");
                let mindArContainer = document.querySelector("#container");

                Init(canvasUnity, mindArContainer, this);
            }
        }
    }
</script>

<style>
    @import '@/css/styles.css';
    @import 'purecss/build/pure.css';
    #container {
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 0;
    }

    .fliped {
        transform: scale(-1, 1);

    }

    #unity-canvas {
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1;
    }
</style>
